html {
  font-size: 20px;
}
body {
  padding: 0;
  margin: 0;
}
ul,
li,
dl {
  padding: 0;
  margin: 0;
  list-style: none;
}
* {
  box-sizing: border-box;
  font-size: 0.6rem;
}
svg {
  font-size: inherit;
}
@media screen and (min-width: 750px) {
  ::-webkit-scrollbar {
    width: 0.2rem;
    height: 0.2rem;
  }
  ::-webkit-scrollbar-button {
    display: none;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: theme_border_radius;
    background: #55befa;
  }
  ::-webkit-scrollbar-track {
    width: 0.3rem;
    height: 0.3rem;
  }
}
.transition {
  transition: all 0.5s;
}
.flex {
  display: flex;
}
.flex_justify {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.flex_column {
  display: flex;
  flex-direction: column;
}
.flex_center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex_1 {
  flex: 1;
}
.mk_divider {
  position: relative;
}
.mk_divider::after {
  content: "";
  position: absolute;
  height: 0.05rem;
  width: 100%;
  background: #e6e6e6;
  transform: scaleY(0.5);
  bottom: 0;
  left: 0;
}
.mk_divider_right {
  position: relative;
}
.mk_divider_right::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 0.05rem;
  background: #e6e6e6;
  transform: scaleX(0.5);
  top: 0;
  right: 0;
}
.mk_divider_top {
  position: relative;
}
.mk_divider_top::after {
  content: "";
  position: absolute;
  height: 0.05rem;
  width: 100%;
  background: #e6e6e6;
  transform: scaleY(0.5);
  top: 0;
  left: 0;
}
.fixed {
  position: -webkit-sticky;
  position: static;
  width: 100%;
}
