.mk_carousel {
  height: 10rem;
  position: relative;
  overflow: hidden;
}
.mk_carousel__item {
  height: 100%;
  width: 100%;
  overflow: hidden;
  flex-shrink: 0;
  transition: all 0.3s;
}
.mk_carousel__item_image {
  width: 100%;
}
.mk_carousel__item.mk_fade {
  position: absolute;
  top: 0;
  left: 0;
}
.mk_carousel__mask {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.8;
  pointer-events: none;
}
.mk_carousel__mask.mk_animate {
  animation: mk_carousel_fadeOut 0.3s forwards;
}
@keyframes mk_carousel_fadeOut {
  from {
    background: #ffffff;
  }
  to {
    background: rgba(255, 255, 255, 0);
  }
}
.mk_carousel_dot {
  position: absolute;
  transition: all 0.3s;
}
.mk_carousel_dot.mk_bottom {
  width: 100%;
  top: calc(100% - 20 * 0.05rem);
  left: 0;
  height: 1rem;
}
.mk_carousel_dot.mk_bottom .mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_bottom .mk_active.mk_rectangle {
  width: 1rem;
}
.mk_carousel_dot.mk_bottomRight {
  top: calc(100% - 20 * 0.05rem);
  left: calc(100% - 20 * 0.05rem);
  height: 1rem;
  transform: translate(-100%, 0);
}
.mk_carousel_dot.mk_bottomRight .mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_bottomRight .mk_active.mk_rectangle {
  width: 1rem;
}
.mk_carousel_dot.mk_bottomLeft {
  top: calc(100% - 20 * 0.05rem);
  left: 1rem;
  height: 1rem;
}
.mk_carousel_dot.mk_bottomLeft .mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_bottomLeft .mk_active.mk_rectangle {
  width: 1rem;
}
.mk_carousel_dot.mk_top {
  width: 100%;
  top: 0;
  left: 0;
  height: 1rem;
}
.mk_carousel_dot.mk_top .mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_top .mk_active.mk_rectangle {
  width: 1rem;
}
.mk_carousel_dot.mk_left {
  width: 1rem;
  top: 0;
  left: 0;
  height: 100%;
}
.mk_carousel_dot.mk_left .mk_carousel_dot__item {
  margin-right: 0;
  margin-bottom: 0.25rem;
}
.mk_carousel_dot.mk_left .mk_carousel_dot__item.mk_rectangle {
  height: 0.8rem;
  width: 0.15rem;
}
.mk_carousel_dot.mk_left .mk_carousel_dot__item.mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_left .mk_carousel_dot__item.mk_active.mk_rectangle {
  height: 1rem;
}
.mk_carousel_dot.mk_right {
  width: 1rem;
  top: 0;
  left: calc(100% - 20 * 0.05rem);
  height: 100%;
}
.mk_carousel_dot.mk_right .mk_carousel_dot__item {
  margin-right: 0;
  margin-bottom: 0.25rem;
}
.mk_carousel_dot.mk_right .mk_carousel_dot__item.mk_rectangle {
  height: 0.8rem;
  width: 0.15rem;
}
.mk_carousel_dot.mk_right .mk_carousel_dot__item.mk_active {
  background: #fff;
}
.mk_carousel_dot.mk_right .mk_carousel_dot__item.mk_active.mk_rectangle {
  height: 1rem;
}
.mk_carousel_dot__item {
  background: #e6e6e6;
  margin-right: 0.25rem;
  cursor: pointer;
  transition: all 0.3s;
}
.mk_carousel_dot__item.mk_rectangle {
  height: 0.15rem;
  width: 0.8rem;
}
.mk_carousel_dot__item.mk_circular {
  height: 0.4rem;
  width: 0.4rem;
  border-radius: 50%;
}
.mk_carousel_dot__item:last-child {
  margin-right: 0;
}
